import { showMask } from '../utils/mask';
import { goToState } from '../state/stateManager';
import { data } from './data';
let courseware = data['courseware'];

const dsBridge = window.dsBridge;

/**
 * 释放答题权限，发生场景在主播老师说GO后执行此api，此时学生进入答题倒数状态，对应打点文件的start.test
 * @param {number} tid 
 * 打点文件的tid
 * @returns {object} 
 * {"type":"read","read_type":"word","read_content":"hello"}
 */
function enableAnswer(tid) {
   
    showMask(false);
    let item = courseware[tid - 1];
    if (item['type'] === 'read') {
        return {
            type: item['type'],
            read_type: item['content_type'] + '' === '0' ? 'sentence' : 'word',
            read_content: item['content']
        };
    } else {
        return {
            type: item['type']
        };
    }

}

/**
 * 关闭答题权限，发生场景在打点文件的stop.test事件
 */
function disableAnswer() {
    showMask(true);
}

/**
 * 跳转到某题，根据打点文件的统一成tid
 * @param {number} ;///注 统一成tid
 */
function jumpQuestion(tid) {
    goToState(tid - 1);
}

/**
 * 返回语音评测分数
 * @param {object} 
 * {"code":"success","score":88.88,"tid":1}
 */
function stopRecord(obj) {

}

/**
 * set目前总星星数
 * @param {number} total_star
 */
function setTotalStar(total_star) {

}

/**
 * 展示上榜的三人
 * @param {array} arr  
 * [{_id: "5ab898001902ed3faaaf657a", lesson_id: 1234, uid: 124, qnum: 2, group: "0",avatar:"http://"}];
 */
function shwoTopThree(arr) {

}

dsBridge.register('enableAnswer', enableAnswer);

dsBridge.register('disableAnswer', disableAnswer);

dsBridge.register('jumpQuestion', jumpQuestion);

dsBridge.register('stopRecord', stopRecord);

dsBridge.register('setTotalStar', setTotalStar);

dsBridge.register('shwoTopThree', shwoTopThree);

export {
    dsBridge
};



