

function showLoading(count, assetsNum, isShow) {

    let mask = window.document.getElementById('abc_time_loading_progress');
    let loading = window.document.getElementById('abc_time_loading');
    if (isShow) {
        loading.style.display = 'block';
    } else {
        loading.style.display = 'none';
    }



    let val = (count / assetsNum) * 100;

    mask.innerHTML = parseInt(val);


}

export { showLoading };


