

function totalAssets(courseware, game) {
    let len = courseware.length;
    let assetsNum = 1;
    for (let i = 0; i < len; i++) {
        let item = courseware[i];
        switch (item['type']) {
            case 'display':

                if (item && item['data'] && item['data']['body']) {
                    let body = item['data']['body'];
                    for (let j = 0; j < body.length; j++) {
                        assetsNum += 1;
                        if (game) {
                            game.load.image(item['id'] + 'body' + j, body[j]['image']);
                        }

                    }
                }

                break;

            case 'survey':
                if (item && item['data'] && item['data']['body']) {
                    let body = item['data']['body'];
                    for (let j = 0; j < body.length; j++) {
                        assetsNum += 1;
                        if (game) {
                            game.load.image(item['id'] + 'body' + j, body[j]['image']);
                        }

                    }
                }

            default:
                ;
        }
    }
    return assetsNum;

}

export { totalAssets };


