import { Phaser } from '../base/Phaser';
import { data } from '../base/data';

let size;

if (data && data['page_size']) {
    size = data['page_size'];
} else {
    size = {
        w: 1280,
        h: 720
    };
}

window.onerror = function (e) {
    console.log(e);
}

const game = new Phaser.Game(size.w, size.h, Phaser.CANVAS, 'root');
game.transparent = true;
export {
    game
};


