import { game } from '../state/game';
import { putVoteData } from '../base/callNative';

const survey = {
    init(data) {
        this.item = data;


    },
    preload() {

    },
    create() {

        let item = this.item;
        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];
            for (let i = 0; i < body.length; i++) {
                let img = game.add.image(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'body' + i);
                img['answer'] = body[i]['name'];
                img.inputEnabled = true;
                img.events.onInputDown.add(function () {

                    putVoteData({ "item": this.answer, "tid": item['id'] });
                }, img)
                //    console.log(img)
            }
        }
    }
};


export { survey };


