import { game } from '../state/game';

const display = {
    init(data) {
        this.item = data;

    },
    preload() {

    },
    create() {
       
        let item = this.item;
        if (item && item['data'] && item['data']['body']) {
            let body = item['data']['body'];
            for (let i = 0; i < body.length; i++) {
               game.add.image(body[i]['pos']['x'], body[i]['pos']['y'], item['id'] + 'body' + i);
            }
        }



    }
};


export { display };


