import { dsBridge } from './registerNative';
const envValue = process.env.NODE_ENV;
/**
 * 课件图片等资源加载完毕调用----用于告诉原生课件资源已经加载完成了。
 * @param {object} obj 课件加载完毕回调事件 
 * {"courseData":"[]"}
 */
function courseLoaded(obj) {
    //同步
    dsBridge.call('echo.courseLoaded', obj);
}

/**
 * 给原生上报获得星星
 * @param {object} obj 
 * 上传得星数 {"num":3,"tid":1} num:星星数 tid:题号
 */
function uploadScore(obj) {
    //同步
    dsBridge.call('echo.uploadScore', obj);
}

/**
 * 给原生上报获得投票
 * 上传投票数据
 * @param {object} obj 
 * {"item":"a","tid":1} item:选项  tid
 */
function putVoteData(obj) {
    //同步
    console.log(obj);
    console.log('投票。。。');

    if (envValue === 'development') {
        console.warn('投票题----->  开发环境不调用原生');
    } else {
        dsBridge.call('echo.putVoteData', obj);
    }

}

/**
 * 获取投票数据
 * @param {object} obj 
 * {"items":"a,b,c","tid":1}  items:所有选项  tid:题号
 * @param {function} callback 
 * callback 参数即为返回值，参数 {"a":12,"b":11,"c":123} a,b,c选项投票结果
 */
function getVoteData(obj, callback) {
    //异步
    dsBridge.call('echo.getVoteData', obj, callback);

}

/**
 * 获取小组列表
 * @param {function} callback 
 *  callback 有一个数组参数 
 * [{"_id":"5ad07fcc27500863e8b6f294","lesson_id":1110255,"uid":111,"qnum":1,"score":3,"nickname":"111","avatar":"http:\/\/file-aliyun.firstleap.cn\/image\/20170926\/1506413631OPVJ.jpg","__v":0,"group":"1"}]
 */
function getRank(callback) {
    //异步
    dsBridge.call('echo.getRank', null, callback);

}



export {
    courseLoaded,
    uploadScore,
    putVoteData,
    getVoteData,
    getRank
};



